"use client"

import { Header } from "@/components/navigation/header"
import { MobileNav } from "@/components/navigation/mobile-nav"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { useCartStore } from "@/lib/cart-store"
import Image from "next/image"
import Link from "next/link"
import { useState } from "react"
import { ChevronLeft, Minus, Plus, ShoppingCart, Heart, Share2, Check } from "lucide-react"
import { useParams } from "next/navigation"

// Mock product data (in real app, fetch from API)
const getProductById = (id: string) => {
  const products = {
    "1": {
      id: "1",
      name: "Huile de Coco Bio",
      price: 15,
      image: "organic+coconut+oil+hair",
      description:
        "Huile de coco 100% pure et biologique, idéale pour nourrir et hydrater vos cheveux en profondeur. Extraite à froid pour préserver tous ses bienfaits naturels.",
      inStock: true,
      ingredients: ["Huile de coco vierge biologique 100%"],
      benefits: ["Hydrate intensément", "Renforce les cheveux", "Prévient les cassures", "Stimule la pousse"],
      usage: "Appliquer sur cheveux secs ou humides. Laisser poser 30 minutes minimum avant le shampoing.",
      volume: "200ml",
    },
    "2": {
      id: "2",
      name: "Beurre de Karité Pur",
      price: 18,
      image: "pure+shea+butter+natural",
      description:
        "Beurre de karité 100% naturel non raffiné. Texture crémeuse qui fond au contact de la peau. Parfait pour sceller l'hydratation.",
      inStock: true,
      ingredients: ["Beurre de karité non raffiné 100%"],
      benefits: [
        "Scelle l'hydratation",
        "Nourrit en profondeur",
        "Protège contre la sécheresse",
        "Apaise le cuir chevelu",
      ],
      usage: "Faire fondre une noisette entre les mains et appliquer sur les longueurs et pointes.",
      volume: "250ml",
    },
  }

  return products[id as keyof typeof products] || products["1"]
}

export default function ProductPage() {
  const params = useParams()
  const product = getProductById(params.id as string)
  const [quantity, setQuantity] = useState(1)
  const [activeImage, setActiveImage] = useState(0)
  const [isAddedToCart, setIsAddedToCart] = useState(false)
  const { addItem } = useCartStore()

  const images = [
    `/placeholder.svg?key=sj4bq&key=rtfzy&height=600&width=600&query=${product.image}`,
    `/placeholder.svg?key=v7j82&key=nzpzk&height=600&width=600&query=${product.image}+detail`,
    `/placeholder.svg?key=l0sbq&key=pzoqq&height=600&width=600&query=${product.image}+texture`,
  ]

  const handleAddToCart = () => {
    for (let i = 0; i < quantity; i++) {
      addItem({
        id: product.id,
        name: product.name,
        price: product.price,
        image: images[0],
      })
    }
    setIsAddedToCart(true)
    setTimeout(() => setIsAddedToCart(false), 2000)
  }

  const relatedProducts = [
    { id: "3", name: "Gel Définition Boucles", price: 22, image: "curl+defining+gel+natural" },
    { id: "4", name: "Spray Hydratant", price: 12, image: "leave+in+conditioner+spray" },
    { id: "5", name: "Bonnet en Satin", price: 8, image: "satin+bonnet+hair+protection" },
  ]

  return (
    <div className="min-h-screen bg-background">
      <Header />

      <main className="pb-20 md:pb-8">
        <div className="container mx-auto px-4 py-6 md:py-8">
          {/* Back Button */}
          <Button variant="ghost" size="sm" className="mb-6" asChild>
            <Link href="/shop">
              <ChevronLeft className="w-4 h-4 mr-1" />
              Retour à la boutique
            </Link>
          </Button>

          {/* Product Details */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-12 mb-16">
            {/* Images */}
            <div className="space-y-4">
              {/* Main Image */}
              <div className="relative aspect-square rounded-lg overflow-hidden bg-muted">
                <Image
                  src={images[activeImage] || "/placeholder.svg"}
                  alt={product.name}
                  fill
                  className="object-cover"
                  priority
                />
              </div>

              {/* Thumbnail Gallery */}
              <div className="grid grid-cols-3 gap-3">
                {images.map((img, idx) => (
                  <button
                    key={idx}
                    onClick={() => setActiveImage(idx)}
                    className={`relative aspect-square rounded-md overflow-hidden bg-muted border-2 transition-colors ${
                      activeImage === idx ? "border-foreground" : "border-transparent"
                    }`}
                  >
                    <Image
                      src={img || "/placeholder.svg"}
                      alt={`${product.name} ${idx + 1}`}
                      fill
                      className="object-cover"
                    />
                  </button>
                ))}
              </div>
            </div>

            {/* Product Info */}
            <div className="space-y-6">
              <div>
                <h1 className="text-3xl md:text-4xl font-bold mb-2">{product.name}</h1>
                <div className="flex items-center gap-4">
                  <p className="text-3xl font-bold">{product.price}€</p>
                  {product.inStock ? (
                    <span className="text-sm text-green-600 flex items-center gap-1">
                      <Check className="w-4 h-4" />
                      En stock
                    </span>
                  ) : (
                    <span className="text-sm text-destructive">Rupture de stock</span>
                  )}
                </div>
              </div>

              <p className="text-muted-foreground leading-relaxed text-pretty">{product.description}</p>

              {/* Volume */}
              <div>
                <p className="text-sm font-medium mb-2">Volume</p>
                <p className="text-sm text-muted-foreground">{product.volume}</p>
              </div>

              {/* Quantity Selector */}
              <div>
                <p className="text-sm font-medium mb-3">Quantité</p>
                <div className="flex items-center gap-3">
                  <Button
                    variant="outline"
                    size="icon"
                    onClick={() => setQuantity(Math.max(1, quantity - 1))}
                    className="h-10 w-10"
                  >
                    <Minus className="w-4 h-4" />
                  </Button>
                  <span className="text-lg font-medium w-12 text-center">{quantity}</span>
                  <Button variant="outline" size="icon" onClick={() => setQuantity(quantity + 1)} className="h-10 w-10">
                    <Plus className="w-4 h-4" />
                  </Button>
                </div>
              </div>

              {/* Actions */}
              <div className="flex gap-3">
                <Button size="lg" className="flex-1 h-12" onClick={handleAddToCart} disabled={!product.inStock}>
                  {isAddedToCart ? (
                    <>
                      <Check className="w-5 h-5 mr-2" />
                      Ajouté au panier
                    </>
                  ) : (
                    <>
                      <ShoppingCart className="w-5 h-5 mr-2" />
                      Ajouter au panier
                    </>
                  )}
                </Button>
                <Button variant="outline" size="icon" className="h-12 w-12 bg-transparent">
                  <Heart className="w-5 h-5" />
                </Button>
                <Button variant="outline" size="icon" className="h-12 w-12 bg-transparent">
                  <Share2 className="w-5 h-5" />
                </Button>
              </div>

              {/* Benefits */}
              <Card className="p-6 border-border">
                <h3 className="font-semibold text-lg mb-3">Bienfaits</h3>
                <ul className="space-y-2">
                  {product.benefits.map((benefit, idx) => (
                    <li key={idx} className="flex items-start gap-2 text-sm">
                      <Check className="w-4 h-4 flex-shrink-0 mt-0.5 text-foreground" />
                      <span>{benefit}</span>
                    </li>
                  ))}
                </ul>
              </Card>

              {/* Usage */}
              <Card className="p-6 border-border">
                <h3 className="font-semibold text-lg mb-2">Mode d'emploi</h3>
                <p className="text-sm text-muted-foreground text-pretty">{product.usage}</p>
              </Card>

              {/* Ingredients */}
              <Card className="p-6 border-border">
                <h3 className="font-semibold text-lg mb-2">Ingrédients</h3>
                <p className="text-sm text-muted-foreground">{product.ingredients.join(", ")}</p>
              </Card>
            </div>
          </div>

          {/* Related Products */}
          <div>
            <h2 className="text-2xl font-bold mb-6">Produits recommandés</h2>
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 md:gap-6">
              {relatedProducts.map((relatedProduct) => (
                <Card
                  key={relatedProduct.id}
                  className="overflow-hidden border-border group cursor-pointer hover:shadow-lg transition-shadow"
                  asChild
                >
                  <Link href={`/shop/${relatedProduct.id}`}>
                    <div className="relative aspect-square overflow-hidden bg-muted">
                      <Image
                        src={`/.jpg?key=btkgo&key=0svzz&height=400&width=400&query=${relatedProduct.image}`}
                        alt={relatedProduct.name}
                        fill
                        className="object-cover group-hover:scale-105 transition-transform duration-300"
                      />
                    </div>
                    <div className="p-4">
                      <h3 className="font-medium text-sm mb-1 line-clamp-2">{relatedProduct.name}</h3>
                      <p className="text-foreground font-semibold">{relatedProduct.price}€</p>
                    </div>
                  </Link>
                </Card>
              ))}
            </div>
          </div>
        </div>
      </main>

      <MobileNav />
    </div>
  )
}
